<template>
	<div class="home_view">
		<div class="projectTitle">欢迎使用 {{projectName}}</div>
		<div class="count_list">
			<el-collapse-transition v-if="btnAuth('xuankezhongxin','首页总数')">
				<el-card v-show="countTypeList.closexuankezhongxinCountType" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								选课中心
							</div>
							<div class="card_head_right">
								<el-icon @click="countTypeClick('hiddenxuankezhongxinCountType')" class="showIcons"
									:class="countTypeList.hiddenxuankezhongxinCountType?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="countTypeClick('closexuankezhongxinCountType')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
							
						</div>
					</template>
					<el-collapse-transition>
						<div class="count_item" v-show="countTypeList.hiddenxuankezhongxinCountType">
							<div class="count_title">选课中心总数</div>
							<div class="count_num">{{xuankezhongxinCount}}</div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('huodongxinxi','首页总数')">
				<el-card v-show="countTypeList.closehuodongxinxiCountType" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								活动信息
							</div>
							<div class="card_head_right">
								<el-icon @click="countTypeClick('hiddenhuodongxinxiCountType')" class="showIcons"
									:class="countTypeList.hiddenhuodongxinxiCountType?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="countTypeClick('closehuodongxinxiCountType')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
							
						</div>
					</template>
					<el-collapse-transition>
						<div class="count_item" v-show="countTypeList.hiddenhuodongxinxiCountType">
							<div class="count_title">活动信息总数</div>
							<div class="count_num">{{huodongxinxiCount}}</div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
		<div class="card_list">
			<el-collapse-transition v-if="btnAuth('xuankexinxi','首页统计')">
				<el-card v-show="cardTypeList.closexuankexinxiChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								选课信息
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenxuankexinxiChartType1')" class="showIcons"
									:class="cardTypeList.hiddenxuankexinxiChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closexuankexinxiChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenxuankexinxiChartType1">
							<div id="xuankexinxikechengleixingEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('chengjixinxi','首页统计')">
				<el-card v-show="cardTypeList.closechengjixinxiChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								成绩信息
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenchengjixinxiChartType1')" class="showIcons"
									:class="cardTypeList.hiddenchengjixinxiChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closechengjixinxiChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenchengjixinxiChartType1">
							<div id="chengjixinxichengjiEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
	</div>
</template>

<script setup>
	import {
		inject,
		nextTick,
		ref,
		getCurrentInstance
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context.$project.projectName
	const countTypeList = ref({
	})
	const getCountList=()=>{
		countTypeList.value.closexuankezhongxinCountType = true
		countTypeList.value.hiddenxuankezhongxinCountType = true
		if(btnAuth('xuankezhongxin','首页总数')){
			getxuankezhongxinCount()
		}
		countTypeList.value.closehuodongxinxiCountType = true
		countTypeList.value.hiddenhuodongxinxiCountType = true
		if(btnAuth('huodongxinxi','首页总数')){
			gethuodongxinxiCount()
		}
	}
	const xuankezhongxinCount = ref(0)
	const getxuankezhongxinCount = () => {
		context?.$http({
			url:'xuankezhongxin/count',
			method: 'get'
		}).then(res=>{
			xuankezhongxinCount.value = res.data.data
		})
	}
	const huodongxinxiCount = ref(0)
	const gethuodongxinxiCount = () => {
		context?.$http({
			url:'huodongxinxi/count',
			method: 'get'
		}).then(res=>{
			huodongxinxiCount.value = res.data.data
		})
	}
	const countTypeClick = (e) => {
		countTypeList.value[e] = !countTypeList.value[e]
	}
	const init=()=>{
		getCountList()
		getCardList()
	}
	//权限验证
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
	let echarts = inject("echarts")
	const cardTypeClick = (e) =>{
		cardTypeList.value[e] = !cardTypeList.value[e]
		setTimeout(()=>{
			getCardList()
		},1000)
	}
	const cardTypeList = ref({
		closexuankexinxiChartType1: true,
		hiddenxuankexinxiChartType1: true,
		closechengjixinxiChartType1: true,
		hiddenchengjixinxiChartType1: true,
	})
	const getCardList = () => {
		if(btnAuth('xuankexinxi','首页统计')){
			getxuankexinxiChart1()
		}
		if(btnAuth('chengjixinxi','首页统计')){
			getchengjixinxiChart1()
		}
	}
	const getxuankexinxiChart1 = () => {
		nextTick(()=>{
			var kechengleixingEchart1 = echarts.init(document.getElementById("xuankexinxikechengleixingEchart1"),'macarons');
			context?.$http({
				url: "xuankexinxi/group/kechengleixing",
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].kechengleixing);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].kechengleixing
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '学生选课课程类型数据统计',
                        left: 'center'
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left'
                    },
                    tooltip: {
						trigger: 'item',
						formatter: '{b} : {c} ({d}%)'
                    },
                    series: [
                        {
                            type: 'pie',
                            radius: '55%',
                            center: ['50%', '60%'],
                            data: pArray,
                            emphasis: {
                                itemStyle: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
									shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                };
				kechengleixingEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				kechengleixingEchart1.setOption(option);
				//根据窗口的大小变动图表
				kechengleixingEchart1.resize();
			})
		})
	}
	const getchengjixinxiChart1 = () => {
		nextTick(()=>{
			var chengjiEchart1 = echarts.init(document.getElementById("chengjixinxichengjiEchart1"),'macarons');
			context?.$http({
				url: "chengjixinxi/sectionStat/chengji/kechengmingcheng",
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis1 = [];
				let yAxis2 = [];
				let yAxis3 = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].kechengmingcheng);
					yAxis1.push(parseFloat((res[i].不及格)));
					yAxis2.push(parseFloat((res[i].良好)));
					yAxis3.push(parseFloat((res[i].优秀)));
					pArray.push({
					    name: res[i].chengji
					})
				}
				var option = {};
                option = {
                    title: {
                        text: '成绩统计',
                        left: 'center'
                    },
                    legend: {
						data: [
							'不及格',
							'良好',
							'优秀',
                        ],
						left: 'center',
						bottom: 10,     
                    },
                    tooltip: {
                      trigger: 'axis'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [
						{
							name: '不及格',
							data: yAxis1,
							type: 'bar'
						},
						{
							name: '良好',
							data: yAxis2,
							type: 'bar'
						},
						{
							name: '优秀',
							data: yAxis3,
							type: 'bar'
						},
                    ],
                };
				chengjiEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				chengjiEchart1.setOption(option);
				//根据窗口的大小变动图表
				chengjiEchart1.resize();
			})
		})
	}
	init()
</script>
<style lang="scss">
	.projectTitle{
		padding: 20px 0;
		font-weight: bold;
		display: flex;
		width: 100%;
		font-size: 30px;
		justify-content: center;
		align-items: center;
		height: 50%;
	}

	.showIcons {
		transition: transform 0.3s;
		margin-right: 10px;
	}

	.showIcons1 {
		transform: rotate(-180deg);
	}
	
	// 总数盒子
	.count_list{
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		// 总数card
		.card_view {
			border: 0px solid #e4e7ed;
			border-radius: 10px;
			box-shadow: none;
			margin: 0 10px 10px;
			flex: 1;
			background: linear-gradient(90deg, #E3EDF9 0%, #D9E6F7 100%);
			width: 100%;
			position: relative;
			box-sizing: border-box;
			height: auto;
			// card头部
			.el-card__header {
				border: 1px solid #e4e7ed;
				padding: 7px 20px;
				background: #62B8FF;
				width: 100%;
				border-width: 0;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					// 标题
					.card_head_title {
						color: #fff;
						font-weight: bold;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon {
							cursor: pointer;
							color: #fff;
							font-weight: bold;
							font-size: 12px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				// body盒子
				.count_item{
					padding: 30px 20px 60px;
					flex-direction: column-reverse;
					display: flex;
					// 总数标题
					.count_title{
						color: #3753A2;
						font-weight: bold;
						font-size: 16px;
						line-height: 1;
						order: 1;
					}
					// 总数数字
					.count_num{
						color: #3753A2;
						font-weight: bold;
						font-size: 50px;
						line-height: 1.5;
						order: 2;
					}
				}
			}
		}
	}
	// 首页盒子
	.home_view {
		padding: 20px;
		background: url(http://clfile.zggen.cn/20240203/09a98f4852104a599055d47953ef0a23.png) no-repeat center top / 100% 100% !important;
		width: 100%;
		min-height: 100vh;
		height: auto;
	}
	// 统计图盒子
	.card_list {
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		// 统计图card
		.card_view {
			border: 0px solid #e4e7ed;
			border-radius: 10px;
			box-shadow: none;
			margin: 0 10px 10px;
			flex: 1;
			background: linear-gradient(90deg, #E3EDF9 0%, #D9E6F7 100%);
			width: 100%;
			position: relative;
			box-sizing: border-box;
			height: auto;
			// 头部
			.el-card__header {
				border: 0px solid #e4e7ed;
				padding: 7px 20px;
				background: #62B8FF;
				width: 100%;
				border-width: 0;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					// 标题
					.card_head_title {
						color: #fff;
						font-weight: bold;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon{
							cursor: pointer;
							color: #fff;
							font-weight: bold;
							font-size: 12px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				// body盒子
				.card_item{
					padding: 30px;
					text-align: center;
				}
			}
		}
	}
</style>
